#largediv{
    display: flex;
    align-items: baseline;
    padding: 0px 10px 0px 25px;
    margin: 0 auto;
    margin-bottom: 50px;
}
#guide{
    width: 35%;
}
#content {
    width: 100%;
    box-sizing: border-box;
    min-width: 200px;
    max-width: 1280px;
    /* margin: 0 auto; */
    padding-left: 20px;
    font-family: Helvetica;
    line-height: 25px;
}

#guide > ul > li > a{
    line-height: 35px;
    text-decoration: none;
    font-family: Helvetica;
    font-size: 16px;
    color: #616670;
    font-weight: 800;
}
#guide > ul > li > a:hover{
    color: #65a1c6;
}
#guide > ul > li{
    list-style-type: none
}
#guide > ul {
    padding-left: 0px;
}
#guide > ul > li > ul > li > a{
    line-height: 35px;
    text-decoration: none;
    font-family: Helvetica;
    font-size: 14px;
    color: #616670;
    font-weight: 700;
}
#guide > ul > li > ul > li > a:hover{
    color: #65a1c6;
}
#guide > ul > li > ul > li{
    list-style-type: disc
}
#guide > ul > li > ul{
    padding-left: 20px;
}
#guide > ul > li > ul > li > ul > li > a{
    font-family: Helvetica;
    font-size: 12px;
    color: #616670;
    font-weight: 400;
    line-height: 30px;
    text-decoration: none;
}
#guide > ul > li > ul > li > ul > li > a:hover{
    color: #65a1c6;
}
#guide > ul > li > ul > li > ul > li{
    list-style-type: none
}
#guide > ul > li > ul > li > ul{
    padding-left: 0px;
}
#guide > ul > li > ul > li > ul > li > ul > li > a{
    font-family: Helvetica;
    font-size: 12px;
    color: #616670;
    font-weight: 400;
    line-height: 30px;
    text-decoration: none;
}
#guide > ul > li > ul > li > ul > li > ul > li > a:hover{
    color: #65a1c6;
}
#guide > ul > li > ul > li > ul > li > ul > li{
    list-style-type: none
}
#guide > ul > li > ul > li > ul > li > ul{
    padding-left: 20px;
}
#guide > ul > li > ul > li > ul > li > ul > li > ul > li > a{
    font-family: Helvetica;
    font-size: 12px;
    color: #616670;
    font-weight: 400;
    line-height: 35px;
    text-decoration: none;
}
#guide > ul > li > ul > li > ul > li > ul > li > ul > li > a:hover{
    color: #65a1c6;
}
#guide > ul > li > ul > li > ul > li > ul > li > ul > li {
    list-style-type: none
}
#guide > ul > li > ul > li > ul > li > ul > li > ul{
    padding-left: 20px;
}
#content > table {
    border-collapse:collapse;
}
#content > table,th,td {
    border: 1px solid #D0D7DE;
}
#content > table > tbody > tr > td{
    padding:10px;
}
#content > table > thead > tr > th{
    padding:10px;
}
#content > pre{
    background-color: #F6F8FA ;
    line-height: 25px;
    font-family: SFMono-Regular;
    font-size: 14px;
    color: #000000;
    font-weight: 400;
    padding-left: 10px;
}
#content > h2{
    font-family: Helvetica;
    font-size: 28px;
    color: #000000;
    font-weight: 700;
    margin-top: 28px;
}
#content > h3{
    font-family: Helvetica;
    font-size: 18px;
    color: #000000;
    font-weight: 700;
}
#content > h4{
    font-family: Helvetica;
    font-size: 16px;
    color: #000000;
    font-weight: 400;
}
#content > p{
    font-family: Helvetica;
    font-size: 16px;
    color: #373a3c;
    font-weight: 400;
}
#content > p > a{
    font-family: Helvetica;
    font-size: 16px;
    color: #000000;
    font-weight: 400;
}

@media (max-width:1020px){
    #largediv {
        padding: 0px;
    }
    #guide{
        display: none;
    }
    #content{
        padding: 0px 10px 0px 20px;
        width: 100%;
    }
    #content > pre{
        line-height: 20px;
    }
    #content > table > tbody > tr > td {
        padding: 5px;
    }
}
@media (max-width: 850px) {
    #content > h2{
        font-size: 24px;
    }
    #content > h3{
        font-size: 16px;
        margin: 5px 0px;
    }
    #content > h4{
        font-size: 14px;
        margin: 5px 0px;
    }
    #content > h5{
        font-size: 12px;
        margin: 5px 0px;
    }
    #content > p {
        line-height: 20px;
        font-size: 12px;
        margin: 5px 0px;
    }
    #content > p > a{
        line-height: 20px;
        font-size: 12px;
    }
    #content > pre {
        font-size: 12px;
    }
    #content > table > thead > tr > th {
        font-size: 12px;
    }
    #content > table > tbody > tr > td {
        font-size: 12px;
        line-height: 20px;
    }
    #content > ol {
        font-size: 12px;
        line-height: 20px;
    }

}